.nav {
	position: fixed;
	background-color: #343A40;
	width: 100%;
	height: 64px;
	display: -webkit-flex;
	display: flex;
	font-family: 'Spartan', sans-serif;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-content: center;
	        align-content: center;
	-webkit-align-items: center;
	        align-items: center;

}

.navTitle a {
	text-decoration: none;
	color: #DEEDF0;
	margin-left: 3vw;
	font-size: 25px;
	font-family: 'Spartan', sans-serif;
	transition: all 0.5s;
}

.navTitle a:hover {
	letter-spacing: 2px;
}

.navLinks a {
	text-decoration: none;
	color: #FFF5FD;
	margin-right: 5vw;
	font-size: 17px;
	transition: all 0.5s;
}

.navLinks a:hover {
	letter-spacing: 2px;
	font-size: 18px;
}

a.left {
	position: relative;
}

a.left:before {
	content: "";
	position: absolute;
	width: 0;
	height: 1px;
	bottom: 0;
	left: 0;
	background-color: #DEEDF0;
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}

a.left:hover:before {
	visibility: visible;
	width: 110%;
}

