.typewriter-text {
	display: inline-block;
	overflow: hidden;
	letter-spacing: 3px;
	-webkit-animation: typing 5s steps(30, end), blink 0.5s step-end infinite;
	        animation: typing 5s steps(30, end), blink 0.5s step-end infinite;
	white-space: nowrap;
	font-size: 30px;
	font-weight: 700;
	border-right: 4px solid orange;
	box-sizing: border-box;
}

@-webkit-keyframes typing {
	from {
		width: 0%
	}

	to {
		width: 68%
	}
}

@keyframes typing {
	from {
		width: 0%
	}

	to {
		width: 68%
	}
}

@-webkit-keyframes blink {

	from,
	to {
		border-color: transparent
	}

	50% {
		border-color: orange;
	}
}

@keyframes blink {

	from,
	to {
		border-color: transparent
	}

	50% {
		border-color: orange;
